/* FOOTER WAVE ANIMATION  */

body {
  font-family: Lato, sans-serif;
}

footer {
  width:100vw;
  position:fixed;
  bottom:0px
}
footer div {
  background-color:#4478e3;
  margin: -5px 0px 0px 0px;
  padding:0px;
  color: #fff;
  text-align:center;
}
svg {
  width:100%;
}
.arrow {
  stroke-width: .3px;
  stroke:yellow;
}
.topball {
  -webkit-animation: ball 1.5s ease-in-out;
          animation: ball 1.5s ease-in-out;
  -webkit-animation-iteration-count:infinite;
          animation-iteration-count:infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
  cursor:pointer;
}

.wave {
  -webkit-animation: wave 3s linear;
          animation: wave 3s linear;
  -webkit-animation-iteration-count:infinite;
          animation-iteration-count:infinite;
  fill: #4478e3;
}
.drop {
  fill: transparent;
  -webkit-animation: drop 5s ease infinite normal;
          animation: drop 5s ease infinite normal;
  stroke: #4478e3;
  stroke-width:0.5;
  opacity:.6; 
  -webkit-transform: translateY(80%); 
          transform: translateY(80%);
}
.drop1 {
  -webkit-transform-origin: 20px 3px;
          transform-origin: 20px 3px;
}
.drop2 {
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
  -webkit-animation-duration:3s;
          animation-duration:3s;
  -webkit-transform-origin: 25px 3px;
          transform-origin: 25px 3px;
}
.drop3 {
  -webkit-animation-delay: -2s;
          animation-delay: -2s;
  -webkit-animation-duration:3.4s;
          animation-duration:3.4s;
  -webkit-transform-origin: 16px 3px;
          transform-origin: 16px 3px;
}
.gooeff {
  	-webkit-filter: url(#goo);
  	        filter: url(#goo);
}
#wave2 {
  -webkit-animation-duration:5s;
          animation-duration:5s;
  animation-direction: reverse;
  opacity: .6
}
#wave3 {
  -webkit-animation-duration: 7s;
          animation-duration: 7s;
  opacity:.3;
}
@-webkit-keyframes drop {
  0% {
    -webkit-transform: translateY(80%);
            transform: translateY(80%); 
    opacity:.6; 
  }
  80% {
    -webkit-transform: translateY(80%);
            transform: translateY(80%); 
    opacity:.6; 
  }
  90% { 
    -webkit-transform: translateY(10%) ; 
            transform: translateY(10%) ; 
    opacity:.6; 
  }
  100% { 
    -webkit-transform: translateY(0%) scale(1.5); 
            transform: translateY(0%) scale(1.5);  
    stroke-width:0.2;
    opacity:0; 
  }
}
@keyframes drop {
  0% {
    -webkit-transform: translateY(80%);
            transform: translateY(80%); 
    opacity:.6; 
  }
  80% {
    -webkit-transform: translateY(80%);
            transform: translateY(80%); 
    opacity:.6; 
  }
  90% { 
    -webkit-transform: translateY(10%) ; 
            transform: translateY(10%) ; 
    opacity:.6; 
  }
  100% { 
    -webkit-transform: translateY(0%) scale(1.5); 
            transform: translateY(0%) scale(1.5);  
    stroke-width:0.2;
    opacity:0; 
  }
}
@-webkit-keyframes wave {
  to {-webkit-transform: translateX(-100%);transform: translateX(-100%);}
}
@keyframes wave {
  to {-webkit-transform: translateX(-100%);transform: translateX(-100%);}
}
@-webkit-keyframes ball {
  to {-webkit-transform: translateY(20%);transform: translateY(20%);}
}
@keyframes ball {
  to {-webkit-transform: translateY(20%);transform: translateY(20%);}
}


